import WorksData from "../works.json";
import classes from "./Works.module.scss";



export const Works = () => {

  const works = [];

  for (const key in WorksData) {
    const workData = WorksData[key];

    const imageStyle = {
      backgroundImage: `url(./img/eyecatch_${key}.png)`
    }

    const work =
            <section key={key} className={classes.section}>
              <div className={classes.imageWrapper}>
                <a href={workData["link"]} target="_blank"><div className={classes.image} style={imageStyle}></div></a>
              </div>
              <div className={classes.info}>
                <h3>{workData["title-jp"]}</h3>
                <p>{workData["description-jp"]}</p>
                <a href={workData["link"]} target="_blank">{workData["link-text"]}</a>
                <div className={classes.date}>{workData.date}</div>
              </div>
            </section>
    ;

    works.push(work);
  }

  return (
    <>
      <div className={classes.title}>
        <h2>WORKS</h2>
        <h5>制作した報道コンテンツや大学院の授業課題など</h5>
      </div>
      <div className={classes.works}>{works}</div>
    </>
  );

};
