import Publications from "../publications.json";
import classes from "./Publication.module.scss";


export const Publication = () => {

  const sections = [];
  Publications.map((pub, i) => {

    const articles = [];

    const k = i.toString();

    pub.articles.map((art, j) => {

      const k = i.toString() + "-" + j.toString();

      const at = (art.link) ? <a href={art.link} target="_blank">{art.text}</a>: art.text;

      const article =
              <article key={k}>
                <div>{art.date}</div>
                <div>{art.media} {at}</div>
              </article>
      ;

      articles.push(article);
    });

    const section =
            <section className={classes.section} key={k}>
              <h3>{pub.title}</h3>
              {articles}
            </section>
    ;

    sections.push(section);
  });

  return (
    <>{sections}</>
  );

};
